@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{
    width: 250px;
    background-color: rgb(252, 190, 71);
    padding-bottom: 0;
    margin:10px;
    border-radius: 20px;
    box-shadow: 3px 5px 8px 0px rgba(0,0,0,0.2);
    transition: transform .2s;
}

.card:hover{
    transform: scale(1.05);
    box-shadow: 5px 10px 15px 0px rgba(0,0,0,0.2);

}

.master{
    float:right;
}

.image{
    width:190px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.title{
    text-align: center;
    margin:0;
}

.card-container{
    padding-top: 10px;
    background-color: whitesmoke;
    border-radius: 20px;
    padding-bottom: 5px;
}

h3{
    font-family: 'Nunito', sans-serif;
}
.list-container{
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}
.search-container{
    display:flex;
    justify-content: center;
    margin-bottom: 20px;
}

.search-box{
    border-radius: 20px;
    border: solid rgb(252, 190, 71) ;
    width: 250px;
    text-align: center;
    height : 40px;
    font-size: 20px;
    color: rgb(252, 190, 71);
    outline: none;
}

::-webkit-input-placeholder{
    color: rgb(252, 190, 71);
    opacity: 0.25;
}

:-ms-input-placeholder{
    color: rgb(252, 190, 71);
    opacity: 0.25;
}

::-ms-input-placeholder{
    color: rgb(252, 190, 71);
    opacity: 0.25;
}

::placeholder{
    color: rgb(252, 190, 71);
    opacity: 0.25;
}
@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: lighter;
    src: local('SEGA LOGO FONT'), url(/react-robo-friends/static/media/SEGA.12a3db8b.woff) format('woff');
}

.header{
    color:rgb(252, 190, 71);
    text-align: center;
    font-family: 'SEGA LOGO FONT';
    font-size: 35px;

}

.body{
    outline: none;
}
